<template>
  <div class="whoami">
    <div class="whoami-wrap">
        <div class="whoami-title">
          <p class="animated fadeInUp">WHO AM I</p>
        </div>
        <div class="whoami-content">
          <p>Hello, I'm Vito Li.</p>
          <p>A 23-year-old research work student in Master, majoring in Information Hiding.</p>
          <p>with a passion for web design, I learnd a lot about front-end and back-end things.</p>
          <p>Art fascinates me, I'm also a photographer and a painter.</p>
          <p>Nice to e-meet you.</p>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeWhoami',
  methods: {
    handleView () {
      var whoamiContent = document.getElementsByClassName('whoami-content')[0]
      var visibleBottom = window.scrollY + document.documentElement.clientHeight
      var visibleTop = window.scrollY
      var centerY = whoamiContent.offsetTop + (whoamiContent.offsetHeight / 2)
      if (centerY > visibleTop && centerY < visibleBottom) {
        whoamiContent.classList.add('animated')
        whoamiContent.classList.add('fadeIn')
      } else {
        // console.log('区域不可见');
      }
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleView)
  }
}
</script>

<style scoped>
.whoami {
  background-color: black;
  overflow: hidden;
  width: 100%;
  height: 0;
  padding-bottom: 30%;
}
.whoami-wrap {
  display:flex;
  justify-content: center;
  align-items: stretch;
  flex-direction: column;
}
.whoami-title {
    text-align: center;
    height: 2rem;
    color: black;
    background-color: #e21c21;
}
.whoami-title p {
    font-size: 1rem;
    position: relative;
    top: 1.15rem;
    font-weight: 600;
}
.whoami-content {
    margin-top: .7rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.whoami-content p {
    text-transform: uppercase;
    color: #ffffff;
    font-size: .5rem;
    line-height: .6rem;
}
</style>



