<template>
  <div class="edu-bg">
    <div class="edu-solgan">
      <div class="edu-solgan-content">
        who am I
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeEducation',
  methods: {
    handleScroll () {
      var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      var solgan = document.querySelector('.edu-solgan')
      if (scrollTop > 1000) {
        solgan.classList.add('isFixed')
      } else {
        solgan.classList.remove('isFixed')
      }
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
  }
}
</script>

<style scoped>
.edu-bg {
  background: black;
  width: 100%;
  height: 5000px;
}
.edu-solgan {
  background: white;
  overflow: hidden;
  background-image: url(../../../assets/img/bg1.jpg);
  background-repeat: no-repeat;
  background-size:auto 1000px;
  width: 30%;
  height: 100%;
}
.isFixed {
  position: fixed;
  background-size: cover;
  top: 0;
}
.edu-solgan-content {
  position: relative;
  text-align: center;
  top: 50%;
  color: #e21c21;
  font-size: 48px;
  font-weight: 600;
}
</style>



