<template>
    <div class="profile-info">
        <div class="profile-img">
            <img src="../../../assets/img/profile.jpg" alt="profile-img">
        </div>
        <div class="profile-desp">
            <h2>单引</h2>
            <h3>Vito</h3>
            <p>手绘 / 板绘 / 篮球 / 摄影 / 编程届的小学生</p>
            <p><a href="mailto:uirlixiao@163.com">uirlixiao@163.com</a></p>
            <p>+86-132-6156-9628</p>
        </div>
    </div>
</template>

<script>
export default {
  name: 'HomeWelcome'
}
</script>

<style>
a {
    text-decoration: none;
    color: white;
}
a:hover {
    color: aqua;
}
.profile-info {
    width: 100%;
    height: 87vh;
    background-color: #e21c21;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.profile-img {
    position: relative;
    margin: 10px;
    width: 16%;
}
.profile-img img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    box-shadow: 5px 5px 30px 0px #270404;
}
.profile-desp h2 {
    font-size: .64rem;
    line-height: 1rem;
    color: white;
    text-align: center;
}
.profile-desp h3 {
    font-size: .36rem;
    line-height: .64rem;
    color: #cccccc;
    text-align: center;
}
.profile-desp p {
    font-size: .36rem;
    line-height: .64rem;
    color: #ffffff;
    text-align: center;
}

</style>



